define('oxide/views/application', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].View.extend({
        removeLoader: (function () {
            var $loader = $(".background-loader");

            if ($loader.length) {
                $loader.remove();
            }
        }).on("didInsertElement")
    });

});